
@import "../static/iconfont.scss";
/***按钮样式   start  ***/
[class^="btn-"][data-v-905e65d4],
.btn[data-v-905e65d4] {
  display: inline-block;
  width: 120px;
  height: 40px;
  font-size: 14px;
  outline: none;
  line-height: 30px;
  box-sizing: border-box;
  text-align: center;
  color: #666;
  cursor: pointer;
  background-color: #F5F5F5;
  border: 1px solid #E1E1E1;
}
/*normal 次按钮*/

/*普通主按钮*/
[class^="btn-primary"][data-v-905e65d4],
.btn-primary[data-v-905e65d4] {
  color: white;
  background-color: #34A853;
  border: none;
}

/* 普通幽灵按钮, 幽灵按钮就是没有背景的按钮 */
[class^="btn-ghost"][data-v-905e65d4],
.btn-ghost[data-v-905e65d4] {
  background-color: transparent;
}
/*普通查看更多按钮*/
[class^="btn-more"][data-v-905e65d4],
.btn-more[data-v-905e65d4] {
  padding: 1px 0;
  border: none;
  background-color: white;
}
/*disabled的按钮*/
[class^="btn-disable"][data-v-905e65d4],
.btn-disable[data-v-905e65d4] {
  padding: 1px 0;
  border: none;
  background-color: #ddd;
  cursor: not-allowed;
}
/*delete的按钮*/
[class^="btn-delete"][data-v-905e65d4],
.btn-delete[data-v-905e65d4] {
  padding: 1px 0;
  border: none;
  background-color: #E64340;
  color: #fff;
}
/*warn的按钮*/
[class^="btn-warn"][data-v-905e65d4],
.btn-warn[data-v-905e65d4] {
  padding: 1px 0;
  border: none;
  background-color: #ff9900;
  color: #fff;
}
/*次按钮 ，幽灵按钮，更多按钮的hover状态*/
[class^="btn"][data-v-905e65d4]:hover,
.btn[data-v-905e65d4]:hover,
.btn-more[data-v-905e65d4]:hover,
.btn-ghost[data-v-905e65d4]:hover{
  color: #5CB975;
  border-color: #5CB975;
}
.btn[data-v-905e65d4]:hover{
  background-color: white;
}
[class^="btn-primary"][data-v-905e65d4]:hover,
.btn-primary[data-v-905e65d4]:hover{
  color: white;
  background-color: #5CB975;
}
[class^="btn-disable"][data-v-905e65d4]:hover,
.btn-disable[data-v-905e65d4]:hover {
  color: #666;
  background-color: #ddd;
}
[class^="btn-delete"][data-v-905e65d4]:hover,
.btn-delete[data-v-905e65d4]:hover {
  color: #fff;
  background-color: #e66d72;
}
[class^="btn-warn"][data-v-905e65d4]:hover,
.btn-warn[data-v-905e65d4]:hover {
  color: #fff;
  background-color: #ffb435;
}
.btn-primary .click[data-v-905e65d4],
.btn-primary .disable[data-v-905e65d4],
.btn-ghost .click[data-v-905e65d4],
.btn-ghost .disable[data-v-905e65d4],
.btn-more .click[data-v-905e65d4],
.btn-more .disable[data-v-905e65d4],
.btn-input[data-v-905e65d4],
.btn-input[data-v-905e65d4]:hover{
}

/***按钮样式   end  ***/
.radius[data-v-905e65d4] {
  border-radius: 10px;
}
.radius20[data-v-905e65d4] {
  border-radius: 20px;
}
.active[data-v-905e65d4] {
  display: inline-block;
  animation: rotates 1.2s steps(12, end) infinite;
}
@keyframes rotates {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
    transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
    transform: rotate3d(0, 0, 1, 360deg);
}
}



